.flex-container{
  margin-bottom: 60px;
  width:1500px;
  min-height: 1000px;
  justify-content: center;
  display: flex;
  flex-direction: row;
}

.blank{
min-width:80px;
}

.edit{
  background:#3a554f;
  min-width:400px;
  box-shadow: 0px 0px 3px #333;
  flex-wrap: nowrap;
  border-radius: 5px;
}

.published{
background:rgb(246,246,238);
width: 900px;
box-shadow: 0px 0px 3px #333;
flex-wrap: nowrap;
border-radius: 5px;
}

.tutorialEdit {
  display:block;
  line-height: 15px;
  background-repeat: no-repeat;
  width: 89%;
  border-radius: 5px;
  min-height: 220px;
  background: rgb(255, 255, 255);
  color: black;
  padding: 1px 0px 0px 7px;
  margin: 20px 0px 20px 20px;
border: 1px solid grey;
box-shadow: 0px 0px 5px rgb(34, 49, 46);
}

.title{
max-height:40px;
margin-top:10px;
padding-top:5px;
padding-bottom:5px;
min-height: 20px;
background:rgb(240, 240, 240);
margin-bottom: -5px;
width:832px;
word-wrap: break-word;
overflow: auto;
}

.content{
background:rgb(240, 240, 240);
position: relative;
overflow: auto;
padding-top:5px;
width:830px;
min-height: 120px;
word-wrap: break-word;
max-height: 217px;

}

.tutorialPublished {
  display:block;
  line-height: 15px;
  background-repeat: no-repeat;
  width: 93%;
  border-radius: 5px;
  min-height: 220px;
  background:white;
  color: black;
  padding: 0px 0px 0px 7px;
  margin: 20px 20px 20px 20px;
  border: 1px solid rgb(192, 192, 192);
  box-shadow: 0px 0px 5px rgb(189, 189, 180);
}

.titleInput{
  width: 600px;
  height: 30px;
  border-radius: 5px;
  background-color: rgb(255, 255, 255);
  font-size: 25px;
  border: 1.5px solid rgb(218, 218, 218);
  margin-top: 10px;
  outline: none;
  resize: none;
  box-shadow: none;
  color: black;
}

.contentInput{
  width: 600px;
  border-radius: 5px;
  background-color: rgb(255, 255, 255);
  font-size: 25px;
  border: 1.5px solid rgb(218, 218, 218);
  margin-top: 10px;
  outline: none;
  box-shadow: none;
  resize: none;
  color: black;
}


.filterList{
  margin-left:310px;
  margin-bottom:50px;
  width: 800px;
  height: 30px;
  border-radius: 5px;
  background-color: rgb(255, 255, 255);
  font-size: 25px;
  border: 1.5px solid rgb(218, 218, 218);
  outline: none;
  box-shadow: none;
  color: black; 
}


.eTitle{
display: block;
border:solid white;
min-height:25px;
height:25px;
width:96%;
margin-top:7px;
background:rgb(240, 240, 240);
color: rgb(145, 153, 151);
font-size: 16px;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
font-weight:bold;
resize:vertical;
overflow: auto; 
}

.eTitle:focus ~ .titleEdit{
color: rgb(240, 240, 240);
}

.eContent:focus ~ .contentEdit{
color: rgb(240, 240, 240);
}

.eContent{
resize:vertical;
min-height:87px;
display: block;
border:solid white;
background:rgb(240, 240, 240);
width:96%;
margin-top:-40px;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
font-size: 16px;
}

.titleEdit{
position: relative;
top:-40px;
color:grey;
left:325px;
}

.contentEdit{
position: relative;
color:grey;
bottom:40px;
left:325px;
}

.publishButton{
display: block;
padding-top:7px;
margin-bottom:11px;
padding-bottom:5px;
width:98%;
margin-top:-10px;
border-radius: 5px;
border: 1px solid rgb(219, 231, 214);
background: rgb(219, 231, 214)
}

.saveButton{
margin-top:-7px;
cursor: pointer;
background:rgb(214, 214, 214);
border-radius: 10%;
border:black;
font-size: 25px;
}

.deleteButton{
background: rgb(250, 165, 150);
border:white;
font-size: 25px;
margin-top:-7px;
margin-right:5px;
cursor: pointer;
margin-left: 100px;
